
@import url("../Styles//fonts.css");
.container{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
}
.social_media{
    position: fixed;
    height: 90vh;
    width: 5%;
    background-color: transparent;
    display: flex;
    flex-direction: column;
   
    justify-content: center;
    align-items: center;
    min-width: 100px;
}

.social_media_fakeContainer{
    height: 90vh;
    width: 5%;
    background-color: white;
    display: flex;
    flex-direction: column;
   
    justify-content: center;
    align-items: center;
}



.ul{
    margin: 0px;
    padding: 0px;
    list-style: none;
    list-style-type: none;
   
    background-color: transparent;
    display: flex;
    flex-direction: column;
    gap: 22px;
    justify-content: center;
    align-items: center;
    width: 100%;
   
}
.svg{
 
}

.github{
    width: 20vw;
    height: 5vh;
    // margin: 0px;
    // padding: 0px;
}

.insta{
    width: 20vw;
    height: 5vh;
    margin: 0px;
    padding: 0px;
}
.linkdedin{
    width: 20vw;
    height: 4vh;
    margin: 0px;
    padding: 0px;
}
.mail{
    width: 20vw;
    height: 4.2vh;
    margin: 0px;
    padding: 0px;
}

.home_container{
    position: static;
   
    width: 95%;
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.left_section{
    margin-left: 5%;
    background-color: transparent;
    width: 50%;
    height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    z-index: 10;
}

.learn_more_button{
    width: 20%;
    height: 50px;
    width: 200px;
    border-radius: 10px;
    border-color: transparent;
    background: #FFF;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25) inset;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    text-decoration: none;
    color: #7E7E7E;
}
.Hey{
    color: #7E7E7E;
    font-family: indieFlower;
    font-size: 40px;
    font-size: calc(16px + 1vw);
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.MyName{
    margin: 0px;
    margin-top: -10px;
    font-family: indieFlower;
    font-size: 90px;
    font-size: calc(20px + 3vw);
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    background: linear-gradient(90deg, #FF02B8 8.45%, #050FFF 46.87%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.description{
    color: rgb(61, 61, 61);
    font-family: IM FELL Great Primer SC;
    font-size: 40px;
    font-size: calc(16px + 1vw);
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.right_section{
    background-color: transparent;
    width: 50%;
    height: 90vh;
    display: flex;
    // flex-direction: column;
    justify-content: start;
    align-items: center;

}

.profile_photo{
    width: 90%;
    z-index: 6;
    max-width: 800px;
}

@media only screen and (max-width: 1100px) {
    .home_container {
    flex-direction: column-reverse;
    align-items: center;
    }
    .left_section{
        width: 90%;
        height: auto;
        justify-content: center;
        // align-items: center;
    }
    .right_section{
        width: 100%;
        height: auto;
         justify-content: center;
    }

    .container{
        flex-direction: column-reverse;
        background-color: transparent;
    }
    .social_media{
        width: 100%;
        height: auto;
        flex-direction: row;
        background-color: transparent;
        position: relative;
        top:  25px;
        margin-bottom: 50px;
    }
    .social_media_fakeContainer{
       background-color: transparent;
        width: 100%;
        height: auto;
        
    }
    .ul{
        width: 50%;
        background-color: transparent;
       gap: 0;
     
        flex-direction: row;
      
        align-items: center;
       
        
    }
   
    }
