.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App_header {
  width: 100%;

  background-color: transparent;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
   z-index: 21;
  display: flex;
  flex-direction: row;

}


.body{
  margin: 0px;
}
.circle{
  position: absolute;
  top: 0px;
  right: 0px;
  height: 90vh;
  width: 100%;
  z-index: 0;
  position: fixed;
   top:  -200px;
   height: 140vh;
   min-height: 1000px;

   width: 40%;
   right: -250px;
   display: flex;
   justify-content: center;
}

.nav {
  width: 90%;
  background-color: transparent;
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: pink;
  display: flex;
  flex-direction: row;
}
.ul{
  display: flex;
  flex-direction: row;
  list-style: none;
  gap: 60px;
}
.li{
  font-family: indieFlower;
  color: #EF34BB;
  user-select: none;
  text-decoration:none;
  font-size: 25px;

}

.left_side_header{

  background-color: transparent;
  width: 5%;
  min-width: 100px;
  display: flex;
  justify-content: center;
}
.svg{
  height: 22px;
  color: transparent;
}

.right_side_header{
  background-color: transparent;
  width: 5%;

  display: flex;
  justify-content: center;
  align-items: start;
  min-width: 100px;
}

.select_language{
   width: 90%;
  height: 30px;
  text-align: center;
  border: 0px;
  color: white;
  background-color: transparent;
  border-color: white;
  border-style: none;
z-index: 20;
  appearance: none;
  
}

.select_language:hover{
  border-color: transparent;
  outline:0px;
}
.select_language::after{
  border-color: transparent;
  outline:0px;
}

.select_language::before{
  border-color: transparent;
  outline:0px;
}
.options{
  background-color: white;
  color: black;
  font-size: 13px;
 padding: 24px;
}




.li:hover{
  color: black;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 1100px) {

.App_header{
  min-height: 10vh;
}
  .ul{
    display: none;
  }
  .nav{
    background-color: transparent;
    
  }
}